import { IEditorUi } from "../../ui/IEditorUi";
import { Graph } from "../../ui/Graph";
import { PmfEvents } from "../../util/PmfEvents";
import { Action } from "../Action";
import { ActionKeys } from "../ActionKeys";

export class DistributeHorizontalAction extends Action {
    constructor(editorUi: IEditorUi) {
        super(ActionKeys.pmf_horizontal, editorUi, false);

        this.editorUi.addListener(PmfEvents.LockedChanged, () => {
            this.setEnabled(this.isAlignActionEnabled(this.editorUi.graph));
        });

        this.editorUi.addListener(PmfEvents.SelectionModelChanged, () => {
            this.setEnabled(this.isAlignActionEnabled(this.editorUi.graph));
        });
    }

    execute(evt?: PointerEvent | KeyboardEvent): void {
        let graph = this.editorUi.graph;
        graph.distributeCells(true);
    }

    isAlignActionEnabled(graph: Graph): boolean {
        return this.isUnlocked(graph) && this.isVertexSelected(graph) && graph.getSelectionCount() > 1;
    }

    isUnlocked(graph: Graph): boolean {
        let unlocked = graph.isEnabled() && !graph.isCellLocked(graph.getDefaultParent());
        return unlocked;
    }

    isVertexSelected(graph: Graph): boolean {
        let vertexSelected = false;

        let cells = graph.getSelectionCells();

        if (cells != null) {
            for (let i = 0; i < cells.length; i++) {
                let cell = cells[i];

                if (graph.getModel().isVertex(cell)) {
                    vertexSelected = true;
                }

                if (vertexSelected) {
                    break;
                }
            }
        }

        return vertexSelected;
    }
}